  .main-nav__toggle span,
  .main-nav__toggle span:before,
  .main-nav__toggle span:after {
    position: absolute;
    height: 2px;
    width: 20px;
    background-color: #000;
    left: 0;
    top: 50%;
    margin-top: -1px;
    transition: 0.2s; }
  .main-nav__toggle span:before {
